@mixin dark-body {
    background-color: black;
    color: white;
}
@mixin dark {
    a {
        color: rgb(228, 160, 255);
        &:hover {
            color: rgb(255, 135, 155);
        }
    }
    #themeSwitch, input[type="submit"] {
        &:hover {
            background-color: rgb(94, 75, 148);
        }
    }
    #themeSwitch {
        border-color: rgb(84, 46, 95);
    }
    form {
        & input, textarea {
            background-color: transparent;
            color: inherit;
            border-color: rgb(92, 73, 109);
        }
    }
    .message {
        border-color: rgb(97, 42, 97);
        background-color: rgb(24, 22, 22);
        & a {
            color: rgb(219, 176, 243);
        }
    }
    .previous, .next {
        a {
            &:hover {
                background-color: antiquewhite;
            }
        }
    }
}